import React, { useState } from "react";
import { Link } from "gatsby";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import ToggleDropdown  from "../components/Header/ToggleDropdown";
import { faChevronDown, faClose } from "@fortawesome/free-solid-svg-icons";
import {
  container,
  form_header,
  form_tag,
  contact_image,
  form_row,
  form_group,
  form_label,
  submit_button,
} from '../Styles/contact_form.module.css';
import {
  headerTop,
  topHeaderBg,
  addressConatiner,
  address_container,
  address_items_with_icons,
  social_icons,
  middleHeader,
  middleHeader_items,
  services_container,
  contact_container,
  contact_info,
  contact_info_icon,
  contact_info_firt_child,
  contact_info_second_child,
} from "../Styles/header.module.css";
import {

  header_button,

} from "../Styles/header.module.css";

// ---- Atomic Components ---- //

export const AddressItem = ({ href, icon, text }) => (
  <address className={address_container}>
    <a href={href} className={address_items_with_icons} target="_blank" rel="noopener noreferrer">
      {icon}
      {text}
    </a>
  </address>
);

export const SocialLinks = ({ icons }) => (
  <div className={social_icons}>
    {icons.map((item, index) => (
      <Link key={index} to={item.href || "#"} className="links">
        {item.icon}
      </Link>
    ))}
  </div>
);

export const ContactInfo = ({ href, icon, primary, secondary }) => (
  <a href={href} className={contact_container}>
    <div className={contact_info_icon}>
      <FontAwesomeIcon icon={icon} color="white" size="2x" />
    </div>
    <div className={contact_info}>
      <span className={contact_info_firt_child}>{primary}</span>
      <span className={contact_info_second_child}>{secondary}</span>
    </div>
  </a>
);

export const Logo = ({ src, alt }) => (
  <Link to="/">
    <img src={src} alt={alt} className="h-12" height={54} width={80} />
  </Link>
);

// ---- Composite Components ---- //

export const TopHeader = ({ addresses, socials }) => (
  <div className={topHeaderBg}>
    <div className={`container ${headerTop}`}>
      <div className={addressConatiner}>
        {addresses.map((item, index) => (
          <AddressItem key={index} {...item} />
        ))}
      </div>
      <SocialLinks icons={socials} />
    </div>
  </div>
);




export const MiddleHeader = ({ logo, contacts,  handleToggle, showOverlay, showDrawer, setShowDrawer,
isOpen, toggleDrawer, openDropdown, setOpenDropdown
      }) => (
  
  <div className={middleHeader}>
    <div className={`container ${middleHeader_items}`}>
      <Logo {...logo} />
      <div className={services_container}>
        {contacts.map((item, index) => (
          <ContactInfo key={index} {...item} />
        ))}
      </div>

       <label className="burger" htmlFor="burger">
                <input
                  type="checkbox"
                  id="burger"
                  onChange={toggleDrawer}
                  checked={isOpen}
                />
                <span />
                <span />
                <span />
              </label>
      
              {/* Overlay */}
              {showOverlay && (
                <div
                  className={`overlay ${!isOpen ? "out" : ""}`}
                  onClick={toggleDrawer}
                />
              )}
      
              {/* Drawer */}
              <div className={`drawer ${showDrawer ? "open" : ""}`}>
                <div className="drawer-content">
                  <div className="close_icon" onClick={toggleDrawer}>
                    <FontAwesomeIcon
                      icon={faClose}
                      size="2x"
                      className="fa_icon_close"
                      color="white"
                    />
                  </div>
      
                  <ul className="drawer_items_mobile">
                    <li>
                      <Link to="/">Home</Link>
                    </li>
                    <li>
                      <Link to="/#aboutus">About Us</Link>
                    </li>
                    <li>
                      <Link to="/#services">Services</Link>
                    </li>
                    <li>
                      <ToggleDropdown
                        title="Pages"
                        isOpen={openDropdown === "Pages"}
                        onToggle={handleToggle}
                      />
                    </li>
                    <li>
                      <ToggleDropdown
                        title="Blogs"
                        isOpen={openDropdown === "Blogs"}
                        onToggle={handleToggle}
                      />
                    </li>
      
                    <li>
                      <Link to="/contactus">Contact Us</Link>
                    </li>
                  </ul>
                   <button className={header_button}>Get A Quote</button>
                </div>
              </div>
    </div>
  </div>
);

export const BottomNav = ({ menu }) => (
  <nav className="bottom_header_nav">{menu}</nav>
);

export const HeaderWrapper = ({ top, middle, bottom }) => (
  <header className="">
    {top}
    {middle}
    {bottom}
  </header>
);







export const ContactContainer = ({ children }) => (
  <div className={container}>{children}</div>
);

export const ContactFormHeader = ({ tag, heading }) => (
  <div className={form_header}>
    <span className={form_tag}>{tag}</span>
    <h2>{heading}</h2>
  </div>
);

export const ContactImage = ({ src }) => (
  <div className={contact_image}>
    <img src={src} alt="Senior couple meeting with advisor" />
  </div>
);

export const FormFieldGroup = ({ children }) => (
  <div className={form_row}>{children}</div>
);

export const FormInputGroup = ({
  id,
  name,
  label,
  value,
  onChange,
  type = 'text',
}) => (
  <div className={form_group}>
    <label htmlFor={id} className={form_label}>
      {label}
    </label>
    {type === 'textarea' ? (
      <textarea
        name={name}
        id={id}
        placeholder={`Your ${label}`}
        value={value}
        onChange={onChange}
        required
      />
    ) : (
      <input
        type={type}
        name={name}
        id={id}
        placeholder={label}
        value={value}
        onChange={onChange}
        required
      />
    )}
  </div>
);

export const SubmitButton = () => (
  <button type="submit" className={submit_button}>
    SEND MESSAGE
  </button>
);




