import React from "react";
import { Popover } from "@headlessui/react";
import { Link } from "gatsby";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faChevronDown } from "@fortawesome/free-solid-svg-icons";
import { useLocation } from "@reach/router";

import {
  menu,
  menu_item_left,
  wrapper,
  dropdown_shadow,
  menu_item,
  menu_dropdown,
  header_button,
  menu_dropdown_items,
} from "../../Styles/header.module.css";

// Menu data array
const menuData = [
  {
    title: "Pages",
    links: [
      { name: "Portfolio", to: "/portfolio" },
      { name: "Our Work", to: "/#ourwork" },
      { name: "Testimonials", to: "/testimonials" },
      { name: "Service Areas", to: "/servicearea" },
      { name: "FAQs", to: "/#faq" },
    ],
  },
  {
    title: "Blogs",
    links: [
      { name: "Blog", to: "/blog" },
      { name: "Blog Post", to: "/blogpost" },
    ],
  },
];

// Reusable Popover Menu Component
const PopoverMenu = ({ title, links }) => (
  <Popover>
    {({ open }) => (
      <>
        <Popover.Button
          className={menu_dropdown}
          style={{ color: open ? "#f7921d" : "white" }}
        >
          {title}{" "}
          <FontAwesomeIcon
            icon={faChevronDown}
            color={open ? "#f7921d" : "white"}
            size="1x"
            style={{
              transition: "transform 0.2s",
              transform: open ? "rotate(180deg)" : "rotate(0deg)",
            }}
          />
        </Popover.Button>

        <Popover.Panel transition anchor="bottom" className={dropdown_shadow}>
          <ul className={menu_dropdown_items}>
            {links.map((item, index) => (
              <li key={index}>
                <Link to={item.to}>{item.name}</Link>
              </li>
            ))}
          </ul>
        </Popover.Panel>
      </>
    )}
  </Popover>
);

const HeaderMenu = () => {
  const location = useLocation();
  const currentPath = location.pathname + location.hash;

  // Helper function to check if the path matches current location
  const isActive = (path) => {
    return currentPath === path;
  };

  return (
    <div className={wrapper}>
      <div className={`${menu} container`}>
        <div className={menu_item_left}>
          <Link
            to="/"
            className={menu_item}
            style={{ color: isActive("/") ? "#f7921d" : "white" }}
          >
            Home
          </Link>
          <Link
            to="/#aboutus"
            className={menu_item}
            style={{ color: isActive("/#aboutus") ? "#f7921d" : "white" }}
          >
            About Us
          </Link>
          <Link
            to="/#services"
            className={menu_item}
            style={{ color: isActive("/#services") ? "#f7921d" : "white" }}
          >
            Services
          </Link>

          {/* Reusable Popovers */}
          {menuData.map((menuItem, index) => (
            <PopoverMenu key={index} title={menuItem.title} links={menuItem.links} />
          ))}

          <Link
            to="/contactus"
            className={menu_item}
            style={{ color: isActive("/contactus") ? "#f7921d" : "white" }}
          >
            ContactUs
          </Link>
        </div>

        <button className={header_button}>Get A Quote</button>
      </div>
    </div>
  );
};

export default HeaderMenu;
