import React, { useState } from "react";
import { faPhone, faStopwatch } from "@fortawesome/free-solid-svg-icons";

import HeaderMenu from "./HeaderMenu";
import { TopHeader, MiddleHeader, BottomNav, HeaderWrapper } from "../Branding";
import factory_direct_flooring_logo from "../../assets/logo/factory_direct_flooring_logo.svg"
import {
  FacebookIcon,
  TwitterIcon,
  YoutubeIcon,
  MailIcon,
  MapIcon,
  InstagramIcon,
} from "../../assets/svgs/svgs";
import {  LucideInstagram } from "lucide-react";

 const socialImageUrl =
  "https://imaginative-jelly-477479.netlify.app/dark_hardwood_flooring_deals_boise2.jpg";
const Header = () => {
  const addressItems = [
    {
      href: "mailto:factorydirectflooring2014@gmail.com",
      icon: <MailIcon />,
      text: "factorydirectflooring2014@gmail.com",
    },
    {
      href: "https://www.google.com/maps?q=910+E+Franklin+Rd+%23105,+Meridian,+ID+83642",
      icon: <MapIcon />,
      text: "910 E Franklin Rd #105, Meridian, ID 83642",
    },
  ];

  const socialIcons = [
    { icon: <FacebookIcon /> },
    { icon: <TwitterIcon /> },
    { icon: <YoutubeIcon /> },
    { icon: <LucideInstagram color="white" width={20} height={20}/>},
  ];

  const contactItems = [
    {
      href: "tel:2085194332",
      icon: faPhone,
      primary: "(208) 519-4332",
      secondary: "Phone",
    },
    {
      href: "tel:2085194332",
      icon: faStopwatch,
      primary: "Mon-Fri 9am-5pm",
      secondary: "Working hours",
    },
  ];
  const handleToggle = (title) => {
    setOpenDropdown((prev) => (prev === title ? null : title));
  };
  const toggleDrawer = () => {
    if (!isOpen) {
      // Opening
      setShowOverlay(true);
      setTimeout(() => setShowDrawer(true), 500);
    } else {
      // Closing
      setShowDrawer(false); // hide drawer first
      setTimeout(() => setShowOverlay(false), 1000);
    }
    setIsOpen(!isOpen);
  };
  const logo = {
    src: factory_direct_flooring_logo,
    alt: "Factory Direct Flooring – Boise’s Trusted Discount Flooring Store",
  };
  const [isOpen, setIsOpen] = useState(false);
  const [showOverlay, setShowOverlay] = useState(false);
  const [showDrawer, setShowDrawer] = useState(false);
  const [openDropdown, setOpenDropdown] = useState(null);
  return (
    <>
   
    <HeaderWrapper
      top={<TopHeader addresses={addressItems} socials={socialIcons} />}
      middle={
        <MiddleHeader
          logo={logo}
          contacts={contactItems}
          handleToggle={handleToggle}
          showOverlay={showOverlay}
          setShowOverlay={setShowOverlay}
          showDrawer={showDrawer}
          setShowDrawer={setShowDrawer}
          isOpen={isOpen}
          toggleDrawer={toggleDrawer}
          openDropdown={openDropdown}
          setOpenDropdown={setOpenDropdown}
        />
      }
      bottom={<BottomNav menu={<HeaderMenu />} />}
    />
    </>
  );
};

export default Header;
